import { Link } from 'gatsby';
import React, { FC, useContext } from 'react';
import styled from 'styled-components';
import { device } from '../../../../../utils/layoutBreakpoints';
import { InternalLinks } from '../../../../../utils/links';
import { PrimaryButton } from '../../../Buttons/Buttons';
import { AppContext } from '../../../Layout/Layout';
import {
  headerNavigationItems,
  HeaderNavigationSections,
} from '../.././HeaderNavigation/HeaderNavigationItem/headerNavigationItems';

const StyledServiceNavigation = styled.div`
  height: 100%;
  display: flex;
  font-family: 'Rubik';
  font-weight: 400;
  font-size: 0.8rem;
  align-items: center;

  a:not(:last-child) {
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 8px;
    &:hover {
      opacity: 0.5;
    }
  }

  a:last-child {
    margin-left: 6px;
    button {
      height: 36px;
    }
  }

  @media ${device.desktop} {
    a:last-child {
      margin-left: 12px;
    }
  }
`;

const ServiceNavigation: FC = () => {
  const appContext = useContext(AppContext);

  const loginLink = headerNavigationItems
    .find(
      (section) => section.sectionTitle === HeaderNavigationSections.ServiceNavigation,
    )
    ?.links.find((link) => link.linkId === 'header_navigation_link_login');
    
  return (
    <StyledServiceNavigation>
      <a href={loginLink?.url} target={'__blank'}>
        {loginLink?.title}
      </a>
      <Link to={''} onClick={(e) => {
        e.preventDefault();
        appContext.getADemoModal.setShowGetADemoModal(true)
      }
      }>
        Get a Demo
      </Link>
      <Link to={InternalLinks.ContactUs}>
        <PrimaryButton height="40px" padding="0 20px">
          Contact Us
        </PrimaryButton>
      </Link>
    </StyledServiceNavigation>
  );
};

export default ServiceNavigation;
