import { Link } from "gatsby";
import React, { FC } from "react";
import styled from "styled-components";
import { NavigationItemLink } from "../../../../types/NavigationItem";
import { colors } from "../../../../utils/colors";
import { device } from "../../../../utils/layoutBreakpoints";
import {
  headerNavigationItems,
  HeaderNavigationSections,
} from "../HeaderNavigation/HeaderNavigationItem/headerNavigationItems";

const StyledMainNavigation = styled.nav`
  height: 100%;
  display: flex;
  font-size: 0.8em;

  a {
    display: flex;
    align-items: center;
    padding: 0 6px;
    &:hover {
      opacity: 0.5;
    }
    &[aria-current="page"] {
      opacity: 0.5;
    }
  }

  @media ${device.desktop} {
    a {
      padding: 0 12px;
    }
  }
`;

const dropdownLRPadding = 12;
const dropdownTBPadding = 12;
const SubNavigationDropdown = styled.div`
  background-color: ${colors.primary.white};
  width: auto;
  height: 0;
  top: 64px;
  left: -${dropdownLRPadding}px;
  position: absolute;
  box-shadow: 0px 10px 20px rgba(40, 42, 59, 0.1), 0px 0px 0px 1px rgba(40, 42, 59, 0.1);
  border-radius: 8px;
  box-sizing: border-box;
  opacity: 0;
  overflow: hidden;
  visibility: hidden;
  transition: all 0.5s cubic-bezier(0.3, 0, 0.3, 1), visibility 0s ease 0.5s;
`;

const dropdownHieght = 40 * 4 + dropdownTBPadding * 2; // 4 links plus margins
const SubNavigationContainer = styled.div`
  display: flex;
  position: relative;
  height: 100%;
  a {
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 12px;
    cursor: pointer;
    &:hover {
      opacity: 0.5;
    }
  }
  &:hover {
    ${SubNavigationDropdown} {
      height: ${dropdownHieght}px;
      opacity: 1;
      visibility: visible;
      transition: all 0.5s cubic-bezier(0.3, 0, 0.3, 1), visibility 0s ease 0s;
    }
  }
`;

const SubNavigationDropdownLinks = styled.div`
  display: flex;
  flex-direction: column;
  gap: 0;
  white-space: nowrap;
  padding: ${dropdownTBPadding}px ${dropdownLRPadding}px;
  a {
    height: 40px;
    display: flex;
    align-items: center;
  }
`;

const Arrow = styled.div`
  display: block;
  position: absolute;
  width: 25px;
  height: 25px;
  top: -12px;
  right: 55%;
  background-color: ${colors.primary.white};
  transform: rotate(45deg);
  box-sizing: border-box;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.3);
`;

const MainNavigation: FC = () => {
  const renderLinks = (links: NavigationItemLink[]) => {
    return links.map((link) => 
      link.external ? (
        <a href={link.url} target="_blank" key={link.linkId} rel="noreferrer">
          {link.title}
        </a>
      ) : link.subLinks?.length ? (
        <SubNavigationContainer key={link.linkId}>
          <a>{link.title}</a>
          <SubNavigationDropdown>
            <SubNavigationDropdownLinks>
              {renderLinks(link.subLinks)}
            </SubNavigationDropdownLinks>
          </SubNavigationDropdown>
        </SubNavigationContainer>
      ) : (
        <Link to={link.url} key={link.linkId}>
          {link.title}
        </Link>
      )
    );
  };

  return (
    <StyledMainNavigation>
      {renderLinks(
        headerNavigationItems.find(
          (section) =>
            section.sectionTitle === HeaderNavigationSections.MainNavigation
        )?.links || []
      )}
    </StyledMainNavigation>
  );
};

export default MainNavigation;
